import { CreditCard } from 'lucide-react'

export function LastTransaction() {
  const transactions = [
    { id: 1, card: '*4230', type: 'Credit', date: '17 Mar 2022', status: 'Verified', trend: '+$1,678', logo: 'visa' },
    { id: 2, card: '*5578', type: 'Credit', date: '12 Feb 2022', status: 'Rejected', trend: '-$839', logo: 'mastercard' },
    { id: 3, card: '*4567', type: 'ATM', date: '28 Feb 2022', status: 'Verified', trend: '+$435', logo: 'amex' },
    { id: 4, card: '*5699', type: 'Credit', date: '08 Jan 2022', status: 'Pending', trend: '+$2,345', logo: 'visa' },
    { id: 5, card: '*5699', type: 'Credit', date: '08 Jan 2022', status: 'Rejected', trend: '+$2,345', logo: 'visa' },
  ]

  const getStatusColor = (status: string) => {
    switch(status) {
      case 'Verified': return 'bg-green-100 text-green-700'
      case 'Rejected': return 'bg-red-100 text-red-700'
      case 'Pending': return 'bg-gray-200 text-gray-600'
      default: return 'bg-gray-100 text-gray-600'
    }
  }

  const getCardLogo = (logo: string) => {
    const logos: { [key: string]: string } = {
      visa: 'ðŸ’³',
      mastercard: 'ðŸ’³',
      amex: 'ðŸ’³',
    }
    return logos[logo] || 'ðŸ’³'
  }

  return (
    <div className="bg-white dark:bg-gray-950 border-b border-slate-700 rounded-lg p-6  shadow-sm h-96 flex flex-col">
      <div className="flex justify-between items-center mb-6">
        <div>
          <h3 className="text-lg font-semibold text-gray-800 dark:text-white">Last Transaction</h3>
        </div>
        <button className="text-gray-400 dark:text-white hover:text-gray-600">â‹®</button>
      </div>

      <div className="overflow-y-auto flex-1">
        <table className="w-full">
          <thead>
            <tr className="border-b border-gray-200 dark:bg-black">
              <th className="text-left text-xs font-semibold text-gray-500 dark:text-white uppercase pb-3">Card</th>
              <th className="text-left text-xs font-semibold text-gray-500 dark:text-white  uppercase pb-3">Date</th>
              <th className="text-left text-xs font-semibold text-gray-500 dark:text-white uppercase pb-3">Status</th>
              <th className="text-left text-xs font-semibold text-gray-500 dark:text-white uppercase pb-3">Trend</th>
            </tr>
          </thead>
          <tbody>
            {transactions.map((tx) => (
              <tr key={tx.id} className="border-b border-gray-100 dark:bg-black hover:bg-gray-50 dark:hover:bg-black">
                <td className="py-4 flex items-center gap-2">
                  <span className="text-xl">{getCardLogo(tx.logo)}</span>
                  <div>
                    <p className="text-sm font-medium text-gray-800 dark:text-white">{tx.card}</p>
                    <p className="text-xs text-gray-500 dark:text-white">{tx.type}</p>
                  </div>
                </td>
                <td className="py-4 text-sm text-gray-600 dark:text-white">{tx.date}</td>
                <td className="py-4">
                  <span className={`px-3 py-1 rounded-full text-xs font-medium ${getStatusColor(tx.status)}`}>
                    {tx.status}
                  </span>
                </td>
                <td className={`py-4 text-sm font-medium ${tx.trend.startsWith('+') ? 'text-green-600' : 'text-red-600'}`}>
                  {tx.trend}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  )
}
